import React, { Component } from "react";
import { Link } from "react-router-dom";
import Footer from "./Footer";
import Header from "./Header";
import "../App.css";
import "./Cart.css";

class Cart extends Component {
  state = {
    placeOrder: false,
    cartData: JSON.parse(localStorage.getItem("product-list")),
    cartCount: localStorage.getItem("cartCount"),
  };
  render() {
    let myCartData = this.state.cartData;

    if (myCartData != null) {
      var itemGrid = myCartData.map((item, pos) => {
        return (
          <div className="checkout-card">
            <div>
              <img className="checkout-img" src={item.preview} alt="" />
            </div>
            <div>
              <h4>{item.title}</h4>
              <p>x{item.count}</p>
              <p>Amount: {item.price * item.count}</p>
            </div>
          </div>
        );
      });
    } else {
      myCartData = [];
    }

    var cost = 0;
    for (let i = 0; i < myCartData.length; i++) {
      cost += parseInt(myCartData[i].count) * parseInt(myCartData[i].price);
    }

    const placeOrder = () => {
      console.log("Excuted");
      this.setState({
        cartData: [],
        cartCount: 0,
      });
      localStorage.removeItem("product-list");
      localStorage.setItem("cartCount", "0");
    };

    return (
      <div>
        <Header count={this.state.cartCount} />
        <div id="main-section">
          <h1 id="main-heading">Checkout</h1>
          <div id="content-wrapper">
            <div id="card-list-left">
              <h3 className="section-heading">
                Total Items: <span id="item-count">{myCartData.length}</span>
              </h3>
              <div>{itemGrid}</div>
            </div>
            <div id="card-list-right">
              <h3 className="section-heading">Total Amount</h3>
              <p>
                Total Amount: Rs <span id="amount-count">{cost}</span>
              </p>
              <Link to="/order-placed">
                <button id="btn-place-order" onClick={placeOrder}>
                  Place Order
                </button>
              </Link>
            </div>
          </div>
        </div>
        <Footer />
      </div>
    );
  }
}

export default Cart;
