import React from "react";
import "../App.css";
import "./Productdetails.css";

function ProductDetails(props) {
  console.log(props);
  return (
    <div id="product-wrapper">
      <div id="product-image">
        <div className="image-wrapper">
          <img id="image-preview" src={props.previewImg} alt="" />
        </div>
      </div>
      <div id="product-details">
        <h1 className="product-title">{props.name}</h1>
        <h1 className="product-brand">{props.brand}</h1>
        <h4 className="section-heading">
          Price: Rs <span className="product-price">{props.price}</span>
        </h4>
        <h4 className="section-heading">Description</h4>
        <p className="description">{props.description}</p>
        <h4 className="section-heading">Product Preview</h4>
        <div id="product-images">
          {props.photos.map((item, pos) => {
            return (
              <img
                className={pos === props.currentImage ? "active-image" : null}
                key={pos}
                src={item}
                alt=""
                onClick={() => props.imgOnClick(pos)}
              />
            );
          })}
        </div>
        <button id="btn-cart" onClick={() => props.addToCart(props)}>
          Add to Cart
        </button>
      </div>
    </div>
  );
}
export default ProductDetails;
