import React from "react";
import Header from "./Header";
import "../App.css";
import "./Orderplaced.css";
import Footer from "./Footer";

function OrderPlaced() {
  return (
    <div>
      <Header count={localStorage.getItem("cartCount")} />
      <div id="main-wrapper">
        <div id="tick-wrapper">
          <img src="https://test-hosting-8f9bf.web.app/assets/white-tick.png" alt="white-tick" />
        </div>
        <h1 className="m-heading">Order Placed Successfully!!</h1>
        <p className="sec-heading">We have sent you an email with the order details</p>
      </div>
      <Footer />
    </div>
  );
}
export default OrderPlaced;
