import React, { Component } from "react";
import axios from "axios";
import Header from "./Header";
import Card from "./Card";
import Loader from "./Loader";
import Carouselbar from "./Carouselbar";
import "../App.css";
import "./Homepage.css";
import Footer from "./Footer";

class Homepage extends Component {
  state = {
    cardList: [],
    loadingSatus: true,
  };
  componentDidMount() {
    axios
      .get("https://5d76bf96515d1a0014085cf9.mockapi.io/product")
      .then((response) => {
        console.log(response.data);
        this.setState({ cardList: response.data, loadingSatus: false });
      })
      .catch((err) => {
        console.log(err);
      });
  }
  render() {
    const cardListData = this.state.cardList;

    const clothingGrid = cardListData.map((item) => {
      return item.isAccessory ? null : (
        <Card
          key={item.id}
          id={item.id}
          preview={item.preview}
          title={item.name}
          brand={item.brand}
          price={item.price}
        />
      );
    });

    const accessoryGrid = cardListData.map((item) => {
      return item.isAccessory ? (
        <Card
          key={item.id}
          id={item.id}
          preview={item.preview}
          title={item.name}
          brand={item.brand}
          price={item.price}
        />
      ) : null;
    });

    return (
      <div>
        <Header count={localStorage.getItem("cartCount")} />
        <Carouselbar />
        <div id="clothing-section" className="homepage-section">
          <h3 className="section-heading">Clothings for Men and Women</h3>
          <div id="clothing-wrapper" className="product-wrapper">
            {this.state.loadingSatus ? <Loader /> : clothingGrid}
          </div>
        </div>
        <div id="accessories-section" className="homepage-section">
          <h3 className="section-heading">Accessory for Men and Women</h3>
          <div id="accessories-wrapper" className="product-wrapper">
            {this.state.loadingSatus ? <Loader /> : accessoryGrid}
          </div>
        </div>
        <Footer />
      </div>
    );
  }
}

export default Homepage;
