import React from "react";
import { Link } from "react-router-dom";
import "../App.css";
import "./Card.css";

const Card = (props) => {
  return (
    <div className="product-card">
      <Link to={`/product/${props.id}`}>
        <img className="product-image" src={props.preview} alt={props.title} />
      </Link>
      <div className="product-details">
        <h4>{props.title}</h4>
        <h5>{props.brand}</h5>
        <p>Rs {props.price}</p>
      </div>
    </div>
  );
};

export default Card;
