import React, { Component } from "react";
import { Link } from "react-router-dom";
import "../App.css";
import "./Header.css";

class Header extends Component {
  render() {
    // Setting Cart Value
    let cartValue = localStorage.getItem("cartCount");
    if (cartValue == null) {
      localStorage.setItem("cartCount", "0");
      cartValue = localStorage.getItem("cartCount");
    } else {
      cartValue = localStorage.getItem("cartCount");
      localStorage.setItem("cartCount", cartValue);
    }
    function bars() {
      document.querySelector(".sidebar").classList.toggle("sidebar-active");
      document.getElementById("bars").classList.toggle("toggle");
    }
    return (
      <header>
        <nav id="topbar">
          <div id="left-section">
            <div id="bars" onClick={() => bars()}>
              <div className="line1"></div>
              <div className="line2"></div>
              <div className="line3"></div>
            </div>
            <div id="logo">
              <Link to="/index">
                {" "}
                <span>SHOP</span>LANE{" "}
              </Link>
            </div>
            <Link to="/index">CLOTHING</Link>
            <Link to="/index">ACCESSORIES</Link>
          </div>
          <div id="search-wrapper">
            <i className="fas fa-search"></i>
            <input type="text" placeholder="Search for Clothing and Accessories" id="search-box" />
          </div>
          <div id="right-section">
            <Link to="/checkout">
              <div id="cart-wrapper">
                <p id="cart-count">{this.props.count}</p>
                <a href="/cart/cart.html">
                  <i className="fas fa-shopping-cart"></i>
                </a>
              </div>
            </Link>
            <img
              src="https://icons.iconarchive.com/icons/papirus-team/papirus-status/256/avatar-default-icon.png"
              alt="avatar-default-icon"
            />
          </div>
        </nav>
        <div className="sidebar">
          <div id="search-wrapper-hidden">
            <i className="fas fa-search"></i>
            <input
              type="text"
              placeholder="Search for Clothing and Accessories"
              id="search-hidden"
            />
          </div>
          <a href="#clothing-section">CLOTHING</a>
          <hr />
          <a href="#accessories-section">ACCESSORIES</a>
          <hr />
        </div>
      </header>
    );
  }
}

export default Header;
